export default `
<script>
{
    //entries返回一个可迭代对象，包含数组的键值对
    let fruits = ["Banana", "Orange", "Apple", "Mango"];
    let x = fruits.entries();
    for(item of x){
        write(item)
    }
}
{
    //entries返回一个可迭代对象，包含数组的键
    let fruits = ["Banana", "Orange", "Apple", "Mango"];
    let x = fruits.keys();
    for(item of x){
        write(item)
    }
}
</script>
`



